import React, { useState } from 'react'
import './Nav.scss'
import '../../Styles/global.scss'
import SearchIcon from '@mui/icons-material/Search';
import LocalShippingOutlinedIcon from '@mui/icons-material/LocalShippingOutlined';
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import OpacityIcon from '@mui/icons-material/Opacity';
import InvertColorsOffIcon from '@mui/icons-material/InvertColorsOff';
import PersonIcon from '@mui/icons-material/Person';
function Nav(props) {
    const [text, settext] = useState('')
    const Inputvariable = (ev) => {
        settext(ev.target.value)
        console.log(text)
    }
    const logo = [
        {
            logo: <LocalShippingOutlinedIcon />,
            para: "Track"
        },
        {
            logo: <FavoriteBorderOutlinedIcon/>,
            para: "Wishlist"
        },
        {
            logo: <ShoppingCartOutlinedIcon />,
            para: "Cart"
        },
        {
            logo: <PersonIcon/>,
            para: "Profile"
        },
        
    ]
    return (
        <div className={` navbar navbar-${props.mode} bg-${props.mode} `}>
            <div className="container">
            <div className=" Navbar-logo">
                <img src="https://ii1.pepperfry.com/images/svg/pf-logo-jan18.svg?v=1" alt="Loading" />
            </div>
            <div className="Navbar-searchbar">
                <input type="text" className={`text-${props.mode === 'light'?'secondary':'info'} bg-${props.mode === 'light'?'':'dark'} Navbar-Input`} onChange={Inputvariable} placeholder={props.placeholder} value={text}></input>
                <button type="button" className={`Navbar-button ${props.mode === "light"?"":"logo-active"}`}><SearchIcon style={{fontSize:"26px"}}/></button>
            </div>
            <div className="Navbar-nav">
                {logo.map((item,index) => {
                    return (
                            <div key={index} className="nav-box1">
                                <span className={`nav-logo ${props.mode === "light"?"":"logo-active"}`} >{item.logo}</span>
                                <span className={`nav-text ${props.mode === "light"?"":"active"}`}>{item.para}</span>
                            </div>
                    )
                })}
                <div className={`nav-box1`} onClick={props.modechanger}>
                <span className={`nav-logo ${props.mode === "light"?"":"logo-active"}`} >{props.mode === "light"?<OpacityIcon/>:<InvertColorsOffIcon/>}</span>
                <span className={`nav-text ${props.mode === "light"?"":"active"}`}>modes</span>
                </div>
            </div>
            </div>
        </div>
    )
}

export default Nav
